<template>
  <div class="left-menu">
   <nav  :class="{toggleLDiv: searchTypeToggle}" style="font-size: 0.94em">                                                                                
      <div class="user-container" :style="{'border-bottom-right-radius': searchTypeToggle ? '0.4em' : '0'}" v-if="!noCustomer">
          <div class="tab-container" >
              <!--  <div class="toggleSearchType">
                     <i class="hasIcon" @click="toggleSearchType">
                        <icon v-if="searchTypeToggle" name="caret-right" scale="1.5" class="caretRight"></icon>
                        <icon v-else name="caret-left" scale="1.5" class="caretLeft"></icon>
                      </i>
               </div> -->
               <div  v-for="(item, $index) in searchTypeArr" class="containTab" >
                  <span :class="{selectSpan: searchType === $index}" @click="changeSearchModel($index)">
                      <i class="hasIcon hasImg">
                         <img  v-if="searchType === $index"  :src="'dist/img/face/' + item.activeImg" />
                         <img  v-else :src="'dist/img/face/' + item.img" />
                      </i>
                      {{item.name}}
                   </span>
               </div>
            </div>
         </div>
         <div class="center-menu" v-show="!searchTypeToggle" :class="{noTypeCon : noCustomer}">
              <selects :option="FormOpt[0].options" v-if="FormOpt[0].show" @getSelect="getFroms" style="margin:1em 0em 1em 0.65em;font-size: 0.97em;"></selects>
              <inputs ref="inputs"  :option="FormOpt[1].options" style="margin:1em 0.65em;" v-show="searchType == 0" 
                         @getInput="getFroms" ></inputs>
              <inputs ref="inputs"  :option="FormOpt[2].options" style="margin:1em 0.65em;" v-show="searchType == 1" v-if="FormOpt[2].show"
                         @getInput="getFroms"></inputs>
              <div class="result-container">
                  <div class="userSearchResult" v-show="searchType === 1" v-if="!noCustomer">
                    <div class="form-list userContainer" v-if="allUsers.length == 0" style="display:table">
                        <p style="display: table-cell;vertical-align: middle;font-size:0.83em">请输入查询条件</p>
                    </div>
                    <VuePerfectScrollbar v-show="allUsers.length>0"  class="form-list userContainer">
                       <li v-for="(user, $index) in allUsers" :style="{'border-bottom': $index === allUsers.length - 1 ? '0' : '1px solid #AEB0BC'}">
                          <div class="hasCheckDiv">
                            <input type="checkbox"  class="checkBox" @click="getSelectUser(user)"  :checked="user.selected" />
                            <label class="myCheckbox myCheckBox totalCheck" ></label>
                          </div>
                           <p v-if="user.customerName">
                              <span class="label-span">用户姓名</span>
                              <span class="label-span" v-html="'：&nbsp;&nbsp;' + getHighTip(user, 'customerName')"></span>
                          </p>
                           <p v-if="user.customerAddr">
                              <span class="label-span">用户地址</span>
                              <span class="label-span" :title="user.customerAddr" v-html="'：&nbsp;&nbsp;' + getHighTip(user, 'customerAddr')"> ：&nbsp;&nbsp;{{user.customerAddr}}</span>
                          </p>
                          <p v-if="user.propertyName">
                              <span class="label-span">门牌编号</span>
                              <span class="label-span" :title="user.propertyName" v-html="'：&nbsp;&nbsp;' + getHighTip(user, 'propertyName')"> ：&nbsp;&nbsp;{{user.propertyName}}</span>
                          </p>
                          <p v-if="user.customerContact">
                              <span class="label-span">联系方式</span>
                              <span class="label-span" :title="user.customerContact" v-html="'：&nbsp;&nbsp;' + getHighTip(user, 'customerContact')"> ：&nbsp;&nbsp;{{user.customerContact}}</span>
                          </p>
                     </li>
                    </VuePerfectScrollbar>
                   <div class="selectTips">
                     <div class="hasCheckDiv allSelect">
                         <input type="checkbox"  class="checkBox" @click="getAllSelect()" :checked="allSelectFlag" />
                         <label class="myCheckbox myCheckBox totalCheck" >全选</label>
                     </div>
                     <label class="label-show" >已选 <span style="color:#FBD63F">{{selectedUser.length}}</span> 项</label>
                      <button class="button-operate operateBtns" style="margin-left:0.5em"  @click="submitUser" >确定</button>
                      <button class="button-operate operateBtns" style="margin-right:0" @click="deepSearch">高级</button>
                   </div>
                 </div>
                 <div class="treeContainer">
                   <div style="width: calc(100% - 1em); height:calc(100% - 4.53em);padding: 0em 0.5em;font-size:0.875em">
                      <VuePerfectScrollbar style="width:100%;height:100%;position:relative">
                          <Tree v-if="dataReady" ref="myTree" v-show="searchType === 0" :noCheck="{'key': 'nodeType', 'value': '1'}" :treeOpt="treeOpt" :isInit="true" @getNode="getNodes" :singleSelect="singleSelect" @getSingle="getSingleNode"  @emptyData="noTreeData" @getTotal="getMeterNum" :hasInitSelect="hasInitSelect" @getInitSelect="getSingleNode"></Tree>
                      </VuePerfectScrollbar>
                   </div>
                   <div class="selectTips">
                     <template v-if="!noCustomer">
                       <div class="hasCheckDiv allSelect">
                         <input type="checkbox"  class="checkBox" @click="getAllTreeNode" :checked="treeAllSelectFlag" />
                         <label class="myCheckbox myCheckBox totalCheck" >全选</label>
                       </div>
                       <label class="label-show" >已选 <span style="color:#FBD63F">{{selectedNode.length}}</span> 项</label>
                       <button class="button-operate operateBtns" @click="submitNode">确定</button>
                     </template>
                      <template v-else>
                        <label class="label-show" style="padding-left:1em">{{currentDevice.text}} 共 <span style="color:#FBD63F">{{meterNum}}</span> 只</label>
                        <selects :option="Status" v-if="hasStatus" @getSelect="getFroms" style="float:right" class="noBackS"></selects>
                      </template>
                  </div>
                 </div>
          </div>
      </div>
   </nav>
   <dialogs type="alert" ref="alerts"></dialogs>
    <dialogs type="customer" ref="deepSearch" width="45em">
           <div slot="header">高级搜索</div>
           <div slot="content">
             <ul class="dialog-ul">
               <li v-for="(item, $index) in deepSearchList" class="formList"  :class="item.classes" >
                 <label class="label-show">{{item.label}}</label>
                 <template v-if="item.type === 'input'" >
                   <inputs :ref="item.options.key" :option="item.options" @getInput="getFrom"></inputs>
                 </template>
                 <template v-if="item.type === 'select'">
                   <selects :ref="item.options.key" :option="item.options" @getSelect="getFrom"></selects>
                 </template>
               </li>
             </ul>
           </div>
          <div slot="footer">
            <button class="button-default" @click="deepData">提交</button>
             <button class="button-default cancel-button" @click="cancelSearch">取消</button>
          </div>
      </dialogs>
 </div>
</template>
<script>
  // import 'vue-awesome/icons/caret-right'
  // import 'vue-awesome/icons/caret-left'
  import Tree from './myTree.vue'
  import VuePerfectScrollbar from 'vue-perfect-scrollbar'
  export default {
    data() {
      return {
         currentDevice:{text: '电表', value: '07'},
         searchTypeToggle: false, //收缩左侧栏标识
         deepSearchParam: { //左侧模糊匹配参数
                customerName: '',
                propertyName: '',
                customerContact: '',
                customerAddr: '',
          },
         dataReady: true,
         searchTree: {
            deviceType: '07',
            deviceNo: '',
            onlineStatus: '',
            isImportant: 0
         }, 
         searchUser: {
                customerName: '',
                propertyName: '',
                customerContact: '',
                customerAddr: '',
         },
         searchTypeArr: [ //左侧类型选择
                {img: 'area_normal.png', activeImg: 'area_normal.png', name: '供电区域'},
                {img: 'user_tab_normal.png', activeImg: 'user_tab_normal.png', name: '用户'}
                
         ],
          searchType: 0, //搜索模式下标
          allUsers: [], //模糊匹配出的所有用户
          selectedUser: [], //从搜索结果里面选中的用户
          allSelectFlag: false, //用户全选标识
          treeAllSelectFlag: false, //树的全选标识
          selectedNode: [], //从搜索结果里面选中的设备
          showTree: false,
          treeOpt: {},
          meterNum: 0
      }
    },
    props: ['noCustomer', 'singleSelect', 'hasStatus', 'hasInitSelect',  'isImport'],
    computed: {
      deepSearchList() {
           let width = 'calc(100% - 6em)';
           return [
            {
              classes: 'halfWidth',
              type: 'input',
              isEmpty: true,
              label: '用户姓名',
              options: {
                width: width,
                type: 'normalInput',
                placeHolder: '可输入中文、数字',
                key: 'deepSearchParam.customerName',
                value: this.deepSearchParam.customerName,
                length: 15,
                regular: 'name'
              }
            },
            {
              classes: 'halfWidth hasMarginLi',
              type: 'input',
              isEmpty: true,
              label: '门牌编号',
              options: {
                width: width,
                type: 'normalInput',
                placeHolder: '可输入字母数字、下划线、-',
                value: this.deepSearchParam.propertyName,
                key: 'deepSearchParam.propertyName',
                length: 30,
                limit: 'NCO'
              }
            },
            {
              classes: 'halfWidth ',
              type: 'input',
              isEmpty: true,
              label: '联系电话',
              options: {
                width: width,
                type: 'normalInput',
                value: '',
                placeHolder: '11位手机号',
                key: 'deepSearchParam.customerContact',
                value: this.deepSearchParam.customerContact,
                errorMsg: '格式：11位手机号',
                limit: 'number',
                length: 13
              }
            },
            {
              classes: 'halfWidth hasMarginLi',
              type: 'input',
              isEmpty: true,
              label: '用户地址',
              options: {
                width: width,
                type: 'normalInput',
                value: '',
                placeHolder: '50个字符内',
                key: 'deepSearchParam.customerAddr',
                value: this.deepSearchParam.customerAddr,
                length: 50,
                regular: 'address'
              }
            }
           ]
      },
      FormOpt() {
         return [  //左侧模糊匹配输入框对象
                 {
                    type: 'select',
                    show: this.noCustomer,
                    options: {
                      cs: 'client',
                      width: '7em',
                      data: [
                        {text: '电表', value: '07'},
                        {text: '水表', value: '08'},
                        {text: '气表', value: '09'},
                        {text: '集中器', value: '04'},
                        {text: '转换器', value: '06'},
                        {text: '采集器', value: '05'},
                      ],
                      key: 'searchTree.deviceType'
                    }
                  },

                  {
                        type: 'input',
                        show: true,
                        options: {
                            width: this.noCustomer ? 'calc(100% - 8.95em)' : 'calc(100% - 1.3em)',
                            type: 'typeSearch',
                            key: 'searchTree.deviceNo',
                            value: '',
                            placeholder: '请输入设备号',
                            length: 16,
                            limit: 'number',
                            hideSelect: true,
                            noBorderSet: true
                         }
                  },
                  {
                        type: 'input',
                        show: !this.noCustomer,
                        options: {
                            width: 'calc(100% - 1.3em)',
                            type: 'typeSearch',
                            key: 'searchUser',
                            noBorderSet: true,
                            value: '',
                            placeholder: '请输入关键字',
                            selectOpt: {
                               width: '6.7em',
                               cs: 'client',
                               data: [
                                 {text: '用户姓名', value: 'customerName'},
                                 {text: '用户地址', value: 'customerAddr'},
                                 {text: '门牌编号', value: 'propertyName'},
                                 {text: '联系电话', value: 'customerContact'}],
                               key: 'searchType'
                            }
                            
                       }
                  }
              ]
      }, 
      Status() {
        return  {
          cs: 'client',
          width: '5em',
          classes: 'selectUlTop',
          orgin: 'top',
          data: [
          {text: '全部', value: ''},
          {text: '在线', value: '1'},
          {text: '离线', value: '0'}
          ],
          key: 'searchTree.onlineStatus'
        }
      }
    },
    components: {
      Tree,
      VuePerfectScrollbar
    },
    methods: {
       getHighTip: function (user, index) {
          let name = user[index];
          let key = this.deepSearchParam[index] || this.searchUser[index];
          if(key !== '' && key !== null && name.indexOf(key) !== -1 && name !== '' && typeof name !== 'undefined') {
               let arr = name.split(key);
               name = arr.join('<font class="hasSearchVal">' + key + '</font>')
          }
          return name
       },
       getMeterNum: function (val) {
          this.meterNum = val
       },
       getSingleNode: function (val) {
         this.$emit('getSingleNode', val)
       },
       getTreeOpt: function () { //左侧树
          let url = 'tree/queryMainSubTree';
          let queryType = 'form';
          switch(this.searchTree.deviceNo) {
            case '': {
              url = 'tree/queryMainSubTree';
              queryType = 'form';
              break;
            }
            default: {
              url = 'tree/fuzzyQueryTree';
              queryType = 'object';
            } 
          }
       
          if(this.isImport) {
              this.searchTree.isImportant = 1;
          }
          let param = this.getSearchData(this.searchTree);
          this.treeOpt  = {
             init: true,
             url: url,
             method: 'post',
             param: param,
             queryType: queryType,
             searchVal: [this.searchTree.deviceNo],
             childUrl: 'tree/queryMainSubTree'
          }
       },
       getFrom: function (val) {
         this.getOption(val, true);
       },
       getFroms: function (val) {
         if(val.key === 'searchTree.deviceNo' || val.key === 'searchTree.deviceType' || val.key === 'searchTree.onlineStatus') {
          if((val.val !== this.searchTree.deviceNo && val.key === 'searchTree.deviceNo') || 
             (val.val !== this.searchTree.deviceType && val.key === 'searchTree.deviceType') ||
              (val.val !== this.searchTree.onlineStatus && val.key === 'searchTree.onlineStatus')
            ) {
            if(val.key === 'searchTree.deviceType') {
               this.currentDevice = val.currentSelect;
               this.$emit('getDeviceType', val.val);
            } 
            if(val.key === 'searchTree.onlineStatus') {
               this.$emit('getIsOnline', val.val);
            }
            this.getOption(val, true);
            this.dataReady = false;
            this.treeAllSelectFlag = false;
            this.selectedNode = [];
            this.getTreeOpt();
            let that =this;
            that.$nextTick(function () {
             that.dataReady = true;
           });
          }  
        } else {
          if(val.val !== this.searchUser[val.key.split('.')[1]]) {
              this.searchUser = this.cleanSearchData(this.searchUser);
              this.getFrom(val);
              this.allSelectFlag = false;
              this.selectedUser = [];
              this.getUsers(this.searchUser)
          }
        }
      },
      toggleSearchType: function () {
         this.searchTypeToggle = !this.searchTypeToggle;
      },
      submitUser: function () {
        let user = this.selectedUser.length === 0 ? '' :  this.selectedUser;
        this.$emit('getSelectUser', user) 
      },
      //树形结构选中后确定操作
      submitNode: function () {
        let node = this.selectedNode.length === 0 ? '' :  this.selectedNode;
        if(node.length > 500) {
           this.$refs.alerts.show({
              msg: '选中的设备不能多于500只'
           })
        } else {
            this.$emit('getSelectNode', node)
        }
      },
      noTreeData: function (val) {

         this.$emit('noData', '')
      },
      cancelSearch: function () {
        this.$refs.deepSearch.close();
      },
      deepSearch: function () {
        this.deepSearchParam = this.cleanSearchData(this.deepSearchParam);
        
        this.$refs.deepSearch.show();
      },
      deepData: function () {
         this.selectedUser = [];
         this.searchUser = this.cleanSearchData(this.searchUser);
         this.$refs.inputs.search = '';
         this.getUsers(this.getSearchData(this.deepSearchParam)); 
      },
       //获取或取消设备树上所有的显示设备
      getAllTreeNode: function () {
        let that = this;
        that.$nextTick(function () {
           that.treeAllSelectFlag = !that.treeAllSelectFlag;
           that.$refs.myTree.getAllSelect(that.treeAllSelectFlag, true);
        })
      },
      //获取选中的用户
      getSelectUser: function (user) {
         let len = this.selectedUser.length;
         user.selected = !user.selected;
         if (len === 0) {
            this.selectedUser.push(user.customerNo);
         } else {
           for (let i=0; i<len; i++) {
             let item = this.selectedUser[i];
             if (item === user.customerNo) {
               this.selectedUser.splice(i, 1);
               break;
             }
             if (i === (len - 1)) {
              this.selectedUser.push(user.customerNo);
             }
           }
         }
       },
      //获取所有搜索出来的用户
       getAllSelect: function () {
         let that = this;
         that.$nextTick(function () {
           that.selectedUser = [];
           that.allSelectFlag = !that.allSelectFlag;
           let len = that.allUsers.length;
           that.selectedUser
           for (let i=0; i<len; i++) {
             that.$set(that.allUsers[i], 'selected', that.allSelectFlag);
             if(that.allSelectFlag) {
               that.selectedUser.push(that.allUsers[i].customerNo);
              }
            }
         })
       },
       changeSearchModel: function (index) {
          this.searchType = index;
          this.$emit('getSearchType', index)
       },
        //获取树形节点
       getNodes: function (val) {
          this.selectedNode = val;
       },

       //获取用户列表
       getUsers: function (param) {
         let that = this;
         let queryParam = {};
         if(param) {
           for(let item in param) {
            if(param[item] !== '') {
              queryParam[item] = param[item];
            }
           }
         }
         if(JSON.stringify(queryParam) != "{}") {
           if(that.isImport) {
              queryParam.isImportant = 1;
           } else {
             queryParam.isImportant = 0;
           }
           that.getRequest({
            url: 'meterrelation/queryCustomerInfo',
            method: 'post',
            param: queryParam,
            success: function (data) {
              if(data.data.length > 500) {
                 that.$refs.alerts.show({
                    msg: '搜索出的数据已超过500条，请缩小查询范围'
                 })
              } else {
                 that.allUsers = data.data;
                 that.cancelSearch();
                if(data.data.length == 0) {
                       that.$refs.alerts.show({
                          msg: '没有查询到匹配的数据'
                      })
                  }
              }
            },
            error: function (data) {
              that.allUsers = [];
              that.$refs.alerts.show({
                    msg: data.message
                 })
            }
          })
        } else {
          that.allUsers = [];
        }
       }
    },
    created: function () {
      //this.getUsers();
      this.searchType = this.noCustomer ? 0 : 0;
      this.getTreeOpt();

    }
  }
</script>
<style lang="scss">
  @import "../../assets/main.scss";
  $searchTypeWidth: 2.7em;
  $leftMenu: 19.5em;
  .left-menu{
     box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
     margin-top: 0em;
     margin-left: 0em;
     width: $leftMenu;
     height: calc(100% - 0em);
    // background: rgba(255,255,255, 0.09);
     background: rgba(#3588fa, 0.3);
     border-radius: 0.4em;
     color: white;
     float:left;
     position: relative;
     nav{
      height:100%;
      width:100%;
     }
     .user-container{
        height: 100%;
        display: inline-block;
        float: left;
       // width: $searchTypeWidth;
        box-shadow: 1px 1px 8px rgba(0,0,0,0.3);
        border-top-right-radius: 0.5em;
        border-bottom-left-radius: 0.5em;
      //  border-bottom-right-radius: 0.5em;
        background: rgba(255,255,255,0.15);
        .tab-container{
          //    width:calc(100% - 2em);
          //    border-bottom: 1px solid #DBDAE2;
          //    padding: 0em 1em;
         //background: #516480;
         background: #305ea0;
          border-bottom-left-radius: 0.5em;
          width: 100%;  
          height: 100%;
          .toggleSearchType{
            height: 4.5em;
            width:100%;
            position: relative;
            .hasIcon{
               position: absolute;
               top: calc((4.5em - 1.5em)/2);
               left:  calc((100% - 1.5em)/2);
               z-index: 1;
               margin:0;
               width:1.5em;
               height: 1.5em;
               border-radius: 50%;
               background: #227fdf;
               cursor: pointer;
               .caretRight{
                 margin-left:0.03em
               }
               .caretLeft{
                 margin-left:-0.13em
               }
            }
          }
          .containTab{
            display: table;
            height: 8em;
            width:2.8em
          }
          span{
            // display: inline-block;
            display: table-cell;
            vertical-align: middle;
            font-size:0.87em;
            padding: 1em 30%;
            // margin: 0 0.5em;
            // width: calc(50% - 1.2em - 1em); 
            line-height: 1.15; 
            cursor: pointer;
            // margin-bottom: -1px;
          
            text-align:center;
            .hasImg{
                margin:0 0 0.5em 0em;
            }
          }
          span:first-child{
             //padding: 0.8em 1.5em;
            // width: calc(50% - 1.2em - 3.5em);   
          }
          span.selectSpan{
            //border-left: 2px solid #227fdf;
            background: #227fdf;

            // border-top-left-radius: 0.5em;
            // border-bottom-left-radius: 0.5em;
          }
        }
     }
     .center-menu{
       width: calc(100% - 30px - 22px);
       display: inline-block;
       height: 100%;
       .input-container {
         font-size: 0.93em;
         border:1px solid #ADACBA;
         border-radius: 0.3em;
         .selectBtn .hasIcon{
          margin-right: 3px;
         }
         input,button{
          background: transparent;
          border:0!important
         }
       }
       .mySelect .selectBtn{
         border:1px solid #ADACBA;
         background: transparent
       }
       .typeSelect .selectBtn{
           border: 0
       }
       .noBackS .selectBtn{
           border: 0;
           background: transparent;
           font-size: 1em;
           .hasIcon{
            margin-right:1em
           }
       }
       .result-container{
          height: calc(100% - 2em - 2.75em*0.94);
          float:left;
          width: 100%;
          .userSearchResult{
            height: 100%;
            width: 100%;
            .hasCheckDiv{
              width: 2.1em;
              float:left;
              margin-top: 0.05em;
              input.checkBox{
                height: 1em;
                width: 1em;
                left: 0.8em
              }
              input.checkBox + .myCheckbox{
                 margin:0
              }
             .myCheckbox:before{
               left: -0.5em;
               line-height: 1.1;

             }
            }
           .userContainer{
             height: calc(100% - 4em);
             position: relative;
             float: left;
             overflow: auto;
             width:100%;
             li{
               width: calc(100% - 1em) ;
               padding:0.5em 0;
               margin: 0 0.5em;
               display: table;
               border-bottom:1px solid #AEB0BC;
               p{
                 margin: 0;
                 width: calc(100% - 2.1em);
                 display: block;
                 float: left;
                 span.label-span{
                   min-width: 4.8em;
                   line-height: 2
                 }

               }
              p:after{
                @extend .clearElement
              }
            }
          }
          }
           .selectTips{
               float: left;
               width: calc(100% - 0.1em);
               margin-left:0.1em;
             //  background:rgba(255,255,255, 0.12);
               border-bottom-right-radius:0.5em;
               height: 4em;
               line-height: 4.53;
               .allSelect{
                 font-size: 0.875em;
                  width:max-content;
                  display: block;
                  float:left;
                  //padding-left: 0.2em;
                 .myCheckbox:before{
                   left: -1.7em;
                   font-size: 1.23em;
                   top: -0.2em;
                 }
                 label.myCheckbox{
                    margin:0 0.5em 0 1em!important;
                    top: 0
                 }
               }
               label.label-show{
                 font-size: 0.875em;
                  margin: 0.85em 0;
                  min-width:4em;
               }
               button{
                font-size: 0.875em;
                padding: 0.4em 0;
                float: right;
                margin: 1.1em 1em;
                height: 2.4em;
               }
               button.operateBtns{
                  width:17%;
                  margin: 1.1em 2%;
               }
            }
       }
       
       .treeContainer{
           height:100%;
       }
     }
     .noTypeCon{
       width: calc(100% );
     }
     .hasSearchVal{
        color: #C43829;
     }
  }
</style>


