<template>
  <div>
     <dialogs type="alert" ref="alertes" width="36em"></dialogs>
  </div>
</template>

<script>
export default {
  data() {
    return {
      websock: null,
      timer: null,
      heartbeatTime: null,
      isLink: null,
      reCount: 0,
      callback: {
        collectData: '',
        onOff: ''
      }
    }
  },
  computed: {
   agentData: function () {
    return this.$store.state.linkCheck || 'linkCheck';
  }
},
methods: {
      //链路维持
      LinkCheck() {
        //参数
        //若是ws开启状态
        let that = this;
        if (this.websock.readyState === this.websock.OPEN) {
          this.$store.state.socketLink = true;
          setTimeout(function () {
            that.websocketsend(true)
          }, 300);
        }
        // 若是 正在开启状态，则等待300毫秒
        else if (this.websock.readyState === this.websock.CONNECTING) {
          this.$store.state.socketLink = false;
          let that = this;//保存当前对象this
          setTimeout(function () {
            that.websocketsend(false);
          }, 500);
        //  console.log(that.$store.state.socketLink)
        }
        // 若未开启 ，则等待500毫秒
        else {
          this.$store.state.socketLink = false;
          this.initWebSocket();
          let that = this;//保存当前对象this 
          setTimeout(function () {
            that.websocketsend(false)
          }, 500);
        }
      },
      getInfo: function () {
              let that = this;
              that.getRequest({
                url: 'system/querySystemConfig',
                method: 'post',
                success: function (data) {
                  let Data = data.data;
                  localStorage.systemConfig = JSON.stringify(Data);
                  that.$store.state.sysName = Data.sysName;
                  that.$store.state.copyright = localStorage.copyright = Data.copyright;
                  that.$store.state.balanceTime = Data.balanceDate;
                  that.$store.state.systemConfig = Data;
                  that.$store.socketUrl = localStorage.socketUrl = Data.webSocketUrl;
                  that.systemInfo = Data;
                }
              });
       },
      //初始化weosocket
      initWebSocket() {
        //if (this.$apiPrefixes && this.$apiPrefixes.host && this.$apiPrefixes.host != "") {
          let url = '';
          if(this.$apiPrefixes && this.$apiPrefixes.host && this.$apiPrefixes.host != "") {

            url = this.$apiPrefixes.host;
             //检查是否包含wss 协议头
           if(!/^(wss:\/\/|ws:\/\/)/gi.test(url)){
              url= this.$apiPrefixes.tf+'://'+url;
            }
          } else {
           if (this.$store.state.socketUrl || localStorage.socketUrl) {
             url = this.$store.state.socketUrl == '' ? localStorage.socketUrl : this.$store.state.socketUrl
           }
         }

         if (url !== '') {
           //检查是否包含wss 协议头
           if(!/^(wss:\/\/|ws:\/\/)/gi.test(url)){
            url= 'ws://'+url;
          }

         //ws地址
         const wsuri = url + "/websocket";
         // const wsuri = "ws://10.10.1.107:8088/websocket"
           console.log(wsuri);
         this.websock = new WebSocket(wsuri);
         this.websock.onmessage = this.websocketonmessage;
         // this.websock.onclose = this.websocketclose;
          //链路维持
          this.webSocketLinkCheck();
           this.websock.onerror = this.recallConnect();
          //发送一次数据
          this.LinkCheck();
        }
        else {
          console.log('ws path error!');
        }
      },
      //数据接收
      websocketonmessage(e) {
        
        this.$store.state.acceptTime = new Date();
      	if (this.heartbeatTime != null) {
          clearTimeout(this.heartbeatTime); //关闭重新连接
        }
        if (this.isLink != null) {
          clearTimeout(this.isLink); //关闭重新连接
        }
        this.$store.state.socketLink = true;
        let responData = JSON.parse(e.data);
        console.log(responData);
        //判断session是否过期
        if (responData.dataFlag == 'linkCheck') { 
          if(responData.data) {
              // this.$parent.clearData();
              // this.pageOver();
              // this.$parent.$router.push({path: '/'});
              // this.$store.state.hasBlur = true;
              this.$parent.showLoginAgain();
              return;
          } 
          this.$store.state.systemTime = responData.sysdate
        }
        else {
          //调用功能页面的数据解析
          if(responData.dataFlag != 'linkCheck') {
            let fun = this.$store.state.callback[responData.dataFlag];
            if (fun && fun.success) {
             fun.success(responData);
           }
         }

       }
     },
      isOnLink: function () {
          let that = this;
          let afterDay = '';
          this.isLink = setTimeout(function () {
             if(that.$store.state.acceptTime !== '') {
                let s1 = that.$store.state.acceptTime.getTime(), s2 = new Date().getTime();
                let afterDay = parseInt(s2 - s1) / 1000;
             }
             if(afterDay > 61 || that.$store.state.acceptTime === '') {
                let msg = '长连接已断开，地址为' + that.$store.state.socketUrl + '，若当前地址有误，请在系统参数中重新配置，若地址正确，重新登录系统！';
                that.$refs.alertes.show({
                  msg: msg
                });
                clearTimeout(that.isLink);
             }
          }, 60000)
      },
      //数据发送
      websocketsend(flag) {
       try {

           if(!flag || (flag && this.agentData == 'linkCheck')) {
             console.log('链路检查：' + this.agentData)
             this.websock.send('');
           } else {
             console.log('发送消息：' + this.agentData)
             this.websock.send(this.agentData);
           }
           this.$store.state.linkCheck = 'linkCheck';
             //发送
           this.recallConnect();
         //  this.isOnLink();
          }
         catch (e) {
            this.reCount = this.reCount + 1; //发送失败重发计数
            console.log("send fail "+this.reCount);
            this.$store.state.socketLink = false;
            if(this.reCount > 20) {
              // this.isOnLink();
               this.reCount = 0;
            }
          }
       },
      //关闭
      websocketclose(e) {  //关闭
        console.log("connection closed (" + e.code + ")");
      },
      //链路检测
      webSocketLinkCheck: function () {
        if (this.timer === null) {
          let that = this;
          this.timer = setInterval(function () {
            that.LinkCheck();
          }, 15000);
        }
      },
      reConnect: function () {
        this.$store.state.socketLink = false;
        let that = this;
        that.initWebSocket();
 
      },
      over: function () {
        this.$store.state.socketLink = false;
        this.$store.state.acceptTime = '';
      	if (this.websock != null) {
          this.websock.close();
          this.$store.state.socketLink = false;
        }
      },
      pageOver: function () {
        this.over();
        if (this.timer != null) {
          window.clearInterval(this.timer);
          clearTimeout(this.heartbeatTime);
          clearTimeout(this.isLink);
        }
      },
      recallConnect: function () {
         let that = this;
            if (this.heartbeatTime != null) {
             clearTimeout(this.heartbeatTime); //关闭重新连接
            }
              //发送内容为心跳则等待服务端回复
            this.heartbeatTime = setTimeout(function () {
              //1S 没有回复重新连接
              if (that.websock != null) {
                that.over();
              }
              that.reConnect();
            }, 2000);
           
      }
    },
    created() {
     let that = this;
     that.getInfo();
     setTimeout(function () {
       that.initWebSocket();
     }, 1)
      //初始化websocket

    },
    beforeDestroy: function () {
      this.pageOver();
    },
    watch: {
      '$store.state.socketLink': {
        handler(val) {
            if(!val) {
              this.recallConnect();
            }
        }
      }
    }
  }
  </script>




