'<template>
    <div ref="trys" style="height: calc(100%)" id="mainShow">
      <div v-if="showInfo" class="info-container">
        <info :row="row" @closeDiv="closeDiv" type="1" :deviceTypes="searchParam.deviceType" ref="infoDetail"></info>
      </div>
      <div v-show="!showInfo" class="all-container noback-Container" >
        <searchTree @getSelectUser="getSelectUser" @getSelectNode="getSelectNode" @getSearchType="getSearchType" ></searchTree>
        <div class="TreeRight">
         <div class="operate-div" ref="operateDiv">
          <template v-for="(item, $index) in FormOpt" >
           <!--  <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom" style="margin-right: 0;"></inputs> -->
            <selects ref="selects"  :option="item.options"
               v-show="!(searchType == 0 && item.options.key == 'searchParam.deviceType')"
               v-if=" item.type == 'select'"
              @getSelect="getOption"></selects>
          </template>

        </div>
        <div class="operate-div operateBtn" >
          <!--v-if="controls[5]" v-if="controls[7]" -->
          <button   class="button-operate" @click="measureNow" v-if="controls[5]" ><i class="hasIcon hasImg" >
            <img src="dist/img/measure/button-icon.png" />
          </i>召测数据
          </button>
          <button  v-if="controls[7]" class="button-operate" @click="sendMessage"><i class="hasIcon hasImg">
            <img src="dist/img/measure/mail-icom.png" />
          </i>发送短信
          </button>
        <div style="float: right">
          <selects ref="selects" :option="checkSelOption" @comfim="comfimFunc" @getSelect="getCollectItem"></selects>
        </div>
        </div>

        <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 4.5em)'}">
          <tables ref="myTable" v-if="showTable" :tableOpt="TableOpt" class="tablePadding"
                  @rowDbClick="clickTableRow"></tables>
        </div>
          </div>
      </div>
      <dialogs type="confirm" ref="conformDialog"></dialogs>
      <dialogs type="progressBar" ref="loading"></dialogs>
      <dialogs type="alert" ref="alerts"></dialogs>
      <dialogs type="customer" ref="deepSearch" width="45em">
           <div slot="header">高级搜索</div>
           <div slot="content">
             <ul class="dialog-ul">
               <li v-for="(item, $index) in deepSearchList" class="formList"  :class="item.classes" >
                 <label class="label-show">{{item.label}}</label>
                 <template v-if="item.type === 'input'" >
                   <inputs :ref="item.options.key" :option="item.options" @getInput="getFrom"></inputs>
                 </template>
                 <template v-if="item.type === 'select'">
                   <selects :ref="item.options.key" :option="item.options" @getSelect="getFrom"></selects>
                 </template>
               </li>
             </ul>
           </div>
          <div slot="footer">
            <button class="button-default" @click="deepData">提交</button>
             <button class="button-default cancel-button" @click="cancelSearch">取消</button>
          </div>
      </dialogs>
      <dialogs type="customer" ref="resultData" class="dialogTable " width="54em" maxHeight="600px" >
        <div slot="header">
          召测结果
        </div>
        <div slot="content" class="tableContents">
          <div class="tabs-show tab-div" style="margin:0 0 0.6em 0!important;float:left;">
            <ul>
              <li :class="{'selectedLi': tabIndex === $index}"
                      v-for="(myBtn, $index) in btnGroup" @click="getTab($index)">
                {{myBtn.name}} ( {{myBtn.num}} )
              </li>
            </ul>
          </div>
          <button class="result-button" v-show="tabIndex === 1 && btnGroup[1].num>0 " @click="commandAgain" >
            <i class="hasIcon hasImg">
              <img src="dist/img/measure/button-icon.png" />
            </i>
            再次执行
          </button>
            <div class="tabContent" style="height:340px;float: left;padding-top:0;font-size:1.1em">
              <tables ref="resoluteTable" :tableOpt="resultOpt" @getTotals="getTotals" v-if="resultShow"></tables>
            </div>
        </div>
      </dialogs>
      <dialogs type="customer" ref="myLoading" width="42.5em" class="myCustomerLoad" v-if="showMeasureLoad">
        <div slot="header" >
          召测进度
        </div>
        <div slot="content">
            <div class="loadingLeft">
              <div class="currentPercent">
                <div class="percentLeft">
                  <div class="circleProgress leftCircle "></div>
                </div>
                <div class="percentRight">
                  <div class="circleProgress  rightCircle"></div>
                </div>
              </div>
              <div class="centerShadow">
                  <img src="../../../assets/img/loading/middle-background.png" >
                  <span >{{currentPercent}}/{{selected.length}}</span>
                  <span>...</span>
              </div>
            </div>
           <div class="loadingRight">
             <transition name="fades" mode="out-in">
             <div class="showResult" v-if ="currentMeasureData !== ''">
               <p>

                   <label v-if="currentMeasureData.deviceCollectStatus == 0" >
		     <i class="hasIcon"><icon name="close" style="color:red"></icon></i>
		        表号： {{currentMeasureData.deviceNo}} &nbsp;&nbsp; 召测失败
		    </label>
		   <label v-else-if="typeof currentMeasureData.pr0 !== 'undefined' || typeof currentMeasureData.balance !== 'undefined' || typeof currentMeasureData.dayFreezeP !== 'undefined' ||typeof currentMeasureData.monthFreezeP !== 'undefined' " >
		      <i class="hasIcon"><icon name="check" style="color:green"></icon> </i>
		      表号： {{currentMeasureData.deviceNo}} &nbsp;&nbsp;
		      <template v-if="currentMeasureData.pr0">有功总： {{currentMeasureData.pr0}}  &nbsp;&nbsp;</template>
		      <template v-if="currentMeasureData.balance">剩余金额：￥{{currentMeasureData.balance}}</template>
		      <template v-if="currentMeasureData.dayFreezeP ">日冻结：{{currentMeasureData.dayFreezeP}} </template>
		      <template v-if="currentMeasureData.monthFreezeP ">月冻结：{{currentMeasureData.monthFreezeP}} </template>
                      </label>
               </p>
             </div>
             </transition>
              <p :style="{'line-height': currentMeasureData.length == '' ? 7.5 : 4}">
                <img src="../../../assets/img/loading/angel.png">
                设备正在召测中，请稍后。。。
              </p>
           </div>
        </div>
      </dialogs>
      <!--<webSocket ref="webSocket"></webSocket>-->
    </div>
</template>
<script>
  import {GUID} from '../../../assets/GUID.js'
  import info from '../../childComponent/Infos.vue'
  import webSocket from '../../childComponent/webSocket.vue'
  import searchTree from '../../childComponent/leftSearchMenu.vue'
  import 'vue-awesome/icons/close'
  import 'vue-awesome/icons/check'
  export default {
    data() {
      return {
        searchType: 0,
        currentMeasure: '',
        showMeasureLoad: true,
        resultShow: false,
        tabIndex: 0,//召测结果tab下标
        dataReady: true,//表单的数据清空问题
        deepSearchData: [{label: '设备类型', value: '电表', sign: false, key: 'deviceType'}],//显示查询项
        searchParam: {    //查询表格数据（包含高级查询）
          deviceType: '07',
          isSearchChild: 0,
          isOnline: '',
          guid: '',
          deviceNoList: [],
          customerNoList: [],
          types:""
        },
        deepSearchParam: {    //高级查询
          customerAddr: '',
          propertyName: '',
          jzqNo: '',
          customerName: '',
          customerContact: '',
          meterUserNo: '',
          deviceNo: '',

        },
        searchType: '',
        tableHeight: '', //table容器高度
        stopFlag: 0, //停止召测标识
        timeOut: '',//轮询方法
        moveOnMsg: 'start',//轮询执行的标识
        showInfo: false, //是否显示详情页
        controls: [], //按钮控制
        showTable: false, //延迟加载table组件
        Guid: '',
        dateNow: '',
        tableObj: '',
        selectedIndex: '',
        selected: '',
        TableOpt: {
          column: [],
          rowName: 'data',
          queryParam: {deviceType: '07', isSearchChild: 0},
          url: 'realTimeData/queryListNew',
          method: 'post',
          pageSize: 20,
          pageNumber: 1,
          pageList: [20, 40, 60],
        //  isExport: true,
          isSetExport: true,
          exportURL: 'realTimeData/generateExcel'
        },
        resultOpt: {
          rowName: 'data',
          column: [],
          pageSize: 99999,
          pageNumber: 1,
          url: 'realTimeData/querySuccessList',
          method: 'post',
          isExport: true,
          height: 260,
          exportURL: 'realTimeData/generateStatusExcel',
          noFooter: true
        },
        currentMeasureData: '',
        currentPercent: 0,
        //#region 选择采集框

        collectItem: [],//选择的采集项
        pageCache:{},
        //#endregion
        btnGroup:''
      }
    },
    components: {
      info,
      webSocket,
      searchTree
    },
    methods: {
      getSearchType: function (val) {
          this.searchType = val
      },
      getSelectUser: function (val) {
          this.searchParam.customerNoList = val.length == 0 ?  [] : val;
          this.searchParam.deviceNoList = [];
          this.Guid = this.searchParam.guid = new GUID().newGUID();
          this.getParams(1)
      },
      getSelectNode: function (val) {
          let arr = [];
          if(val.length > 0) {
            for(let j = 0; j<val.length;j++) {
                  arr.push(val[j].nodeId);
            }
          }
          this.searchParam.deviceNoList =  arr.length == 0 ?  [] : arr;
          this.searchParam.customerNoList = [];
          this.Guid = this.searchParam.guid = new GUID().newGUID();
          this.getParams(1)
      },
      closeLoading: function () {
        this.$refs.myLoading.close();
        this.showMeasureLoad = false;
        this.currentMeasureData = '',
          this.currentPercent = 0
      },
      //从详情页面返回的操作
      closeDiv: function () {
        this.showInfo = false;
       // this.$parent.showInfoDetail = true;
        this.$refs.myTable.onResize();
      },
      // 调用公共方法获取自定义弹出框表单的数据
      getFrom: function (val) {
        if (val.combine) {
          if (this.searchParam[val.key.split('.')[1]] !== val.val) {
            this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'guid',  'isOnline']);
            this.getOption(val, true);
            this.getParams(1);
          }
        } else {
          this.getOption(val, true);
        }
      },
      deepSearch: function () {
        this.dataReady = true;
        this.$refs.deepSearch.show();
      },
      //高级搜索查询数据
      deepData: function () {
        this.$refs['inputs'][0].search = '';
        let arr = this.getSearchData(this.deepSearchParam);
        this.searchParam = this.extendObj(this.cleanSearchData(this.searchParam, ['deviceType', 'guid']), arr);
        this.getParams(1);
        this.cancelSearch();
      },
      //关闭并清空所有查询条件
      cancelSearch: function () {
        this.$refs.deepSearch.close();
        this.dataReady = false;
        this.deepSearchParam = this.cleanSearchData(this.deepSearchParam);
      },
      //查询数据,第二个参数为不清空的值
      getParams: function (pageNumber) {
        this.deepSearchData = [];
        let arr = this.getSearchData(this.searchParam);
        arr.isSearchChild = 0;
        arr.types = this.collectItem;
        let labelName;
        for (let key in arr) {
          let value = arr[key];
          let removeFlag = true;
          switch (key) {
            case 'customerName':
              labelName = '用户姓名';
              break;
            case 'customerAddr':
              labelName = '用户地址';
              break;
            case 'propertyName':
              labelName = '门牌编号';
              break;
            case 'customerContact':
              labelName = '联系电话';
              break;
            case 'meterUserNo':
              labelName = '表计户号';
              break;
            case 'deviceNo':
              labelName = '用户表号';
              break;
            case 'jzqNo':
              labelName = '集中器号';
              break;
            case 'isOnline':
              labelName = '是否在线';
              removeFlag = true;
              value = this.getEnumItem('isOnline', value).text;
              break;
            case 'deviceType':
              labelName = '设备类型';
              removeFlag = false;
              value = this.getEnumItem('deviceType', value).text;
              break;
            default:
              labelName = ''
          }
          this.deepSearchData.push({label: labelName, value: value, flag: removeFlag, key: key})
        }
        if(this.$store.state.currentSearchType == 1) {
          arr.deviceType = '07';
        }
        this.getHeight();
        this.$refs.myTable.resetTableOpt({column: this.tblColumns, isLoadData: false});
        this.searchTable(arr, pageNumber);
      },
      //清空查询条件
      removeSearchData: function (index) {
        let key = this.deepSearchData[index].key;
        if (key === 'isOnline') {
          this.$refs['selects'][1].constant = {text: "全部状态", value: ''};
          this.searchParam.isOnline = '';
        } else {
          this.searchParam[key] = this.$refs['inputs'][0].search = '';
          this.getParams(1);
        }
      },
      //召测数据
      measureNow: function () {
        let that = this;
        that.selected = that.selectedIndex = [];
        if (this.collectItem.length > 0) {
          this.getConfirm({
            emptyMsg: '请先选择要召测的用户',
            confirmMsg: '确定要召测选中的用户吗？',
            callback: {
              confirm: function () {
                that.moveOnMsg = 'start';
                that.selected = that.$refs.myTable.getSelected();
                that.selectedIndex = that.$refs.myTable.getSelectedIndex();
                that.firstRequest();
              }
            }
          });
        }
        else {
          this.$refs.alerts.show({
            msg: '请选择采集数据项'
          })
        }
      },
      //获取参数
      getOperateParams: function () {
        let param = {};
        param['guid'] = this.Guid;
        param['meters'] = [];
        param['isImportant'] = 0;
        param['types'] = this.collectItem;

        for (let i = 0; i < this.selected.length; i++) {
          param['meters'].push({
            jzqCno: this.selected[i].jzqCno,
            deviceCno: this.selected[i].deviceCno,
            groupGuid: this.selected[i].groupGuid
          })
        }
        return param;
      },
      //发送短信
      sendMessage: function () {
        this.$parent.sendMessage()
      },

      //单击进入详情页面
      clickTableRow: function (val) {
        this.row = val.val;
        this.showInfo = true;
       // this.$parent.showInfoDetail = false;
      },
      //指令停止以后的部分操作
      operateStop: function (sign) {
        this.closeLoading();
        this.$store.state.callback['collectDataStatus'] = null;
        this.$store.state.callback['collectData'] = null;
        this.$store.state.linkCheck = 'linkCheck';
        clearInterval(this.$store.state.setRecall);
        this.moveOnMsg = 'start';
        this.getParams();
        if(sign) {
          this.resoluteTableShow();
        }
        this.stopFlag = 0;
        this.currentMeasureData = '';
        this.currentPercent = 0;

      },
      //停止召测
      stopMeasure: function () {
        let that = this;
        that.getRequest({
          url: 'realTimeData/stopCollectList',
          method: 'post',
          param: {guid: that.Guid},
          success: function (data) {
            that.operateStop(true);
          },
          error: function (data) {
            that.stopFlag = 0;
            that.$refs.alerts.show({
              msg: data.message
            })
          }
        }, true);
      },
      //指令发送
      firstRequest: function () {
        let that = this;

        this.currentMeasureData = '';
        this.currentPercent = 0;
        let param = that.getOperateParams();
        this.showMeasureLoad = true;
        this.$nextTick(function () {
          that.$refs.myLoading.show({
            hasClose: true,
            action: {
              cancel: function () {
                that.stopMeasure();
              }
            }
          });
        });

        that.getRequest({
          url: 'realTimeData/batchSendCollectInstructions',
          param: param,
          method: 'post',
          success: function (data) {
            that.dateNow = data.data || '';
            that.setAllStatus(3, that.secondRequest())
          },
          error: function (data) {
            that.$refs.myLoading.close();
            that.dateNow = data.data || '';
            that.$refs.alerts.show({
              msg: data.message
            });
            that.getParams();
            that.stopFlag = 0;

          }
        });
      },
      secondRequest: function () {
        let that = this;
        that.getWebsocket({
          keyMsg: 'collectData',
          keyStatus: 'collectDataStatus',
          paramStatus: {
            guid: that.Guid,
            dataFlag: 'collectDataStatus'
          },
          sign: true,
          funMsg: function (data) {
            let datas = data.data;
            that.currentPercent = datas.dealNum;
            if (datas.deviceCollectStatus == 1) {
              that.setStatus(1, datas);
            } else if (datas.deviceCollectStatus == 101) {
              that.currentMeasure = datas;
              that.setStatus(3, datas);
            } else {
              that.setStatus(-1, datas);
            }
            if (datas.taskStatus !== 101) {
                that.operateStop(true);
            }
          },
          funStatus: function (data) {
            that.operateStop(true);
          }
        });
      },

      //初始化设置召测状态
      setAllStatus: function (status, callback) {
        let that = this;
          for (let i = 0; i < that.selected.length; i++) {
            that.tableObj.updateRow(that.selectedIndex[i], {
              readStatus: status
            });
          }
          callback();
      },
      //召测中修改表格数据
      setStatus: function (signal, data) {
        let that = this;
        let deviceNo = '';
        let measureData = data;
        that.$nextTick(function () {
          for (let j = 0; j < that.selected.length; j++) {
            if(data.waitTime && data.waitTime!=''){
               that.pageCache[that.selected[j].deviceCno]=data.waitTime;
            }
            switch (signal) {
              case 3:
                if (that.selected[j].deviceCno === data.cno) {
                  deviceNo = that.selected[j].deviceNo;
                }
                break;
              case 1:
                if (that.selected[j].deviceCno === data.cno) {
                  measureData.deviceNo = that.selected[j].deviceNo;
                  deviceNo = that.selected[j].deviceNo;

                  that.tableObj.updateRow(that.selectedIndex[j], {
                    pr0: data.pr0 || '', 
                    balance: data.balance || '',
                    dayFreezeP: data.dayFreezeP || '',
                    monthFreezeP: data.monthFreezeP || '',
                    collectTime: data.collectTime || '',
                    waitTime: data.waitTime,
                    readStatus: signal
                  });
                }
                break;
              case -1:
                if (that.selected[j].deviceCno === data.cno) {
                  measureData.deviceNo = that.selected[j].deviceNo;
                  deviceNo = that.selected[j].deviceNo;
                  that.tableObj.updateRow(that.selectedIndex[j], {
                    waitTime: data.waitTime,
                    readStatus: signal,
                    collectTime: data.collectTime || '',
                  });
                }
                break
            }
          }
          that.currentMeasureData = {};
          that.currentMeasureData = data;
          that.currentMeasureData.deviceNo = deviceNo;
        });

      },
      //召测结果数量显示
      getTotals: function (val) {
        this.btnGroup[this.tabIndex].num = val;
      },
      //召测结果tab切换页
      getTab: function (index) {
        this.tabIndex = index;
        let p, download;
        p = {guid: this.Guid, deviceType: this.deviceType, searchDate: this.dateNow};
        let column;
        let columns = this.columns;
        if (index === 0) {
          p['flag'] = 1;
          download = true;
          column = columns[0]
        } else {
          p['flag'] = 0;
          download = false;
          column = columns[1]
        }
        this.$refs.resoluteTable.resetTableOpt({
          queryParam: p,
          isExport: download,
          column: column,
          url: this.btnGroup[this.tabIndex].url
        });
      },
      //再次召测
      commandAgain: function () {
        let tables = this.$refs.resoluteTable;
        tables.getAllSelect();
        let selectAgain = tables.getSelected();
        let lenAgain = selectAgain.length;
        let len = this.selected.length;
        let IndexAgain = [];
        for (let i = 0; i < lenAgain; i++) {
          for (let j = 0; j < len; j++) {
            if (this.selected[j].deviceCno == selectAgain[i].deviceCno) {
              IndexAgain.push(this.selectedIndex[j]);
              break;
            }
          }
        }
        this.selected = tables.getSelected();
        this.selectedIndex = IndexAgain;
        this.closeResult();
        this.firstRequest();
      },
      //关闭召测结果窗口
      closeResult: function () {
        this.$refs.resultData.close();
        this.resultShow = false;
        this.tabIndex = 0;
        this.btnGroup[0].num = this.btnGroup[1].num = 0;
      },
      resoluteTableShow: function () {

        this.resultOpt.queryParam = {
         guid: this.Guid,
         deviceType: this.deviceType,
         flag: 1,
         searchDate: this.dateNow
       };
       this.resultOpt.url =  this.btnGroup[0].url;
       this.resultOpt.column =  this.columns[0];
       this.resultShow = true;
       this.$refs.resultData.show({
        hasClose: true,
        action: {
          cancel: function () {
            that.closeResult()
          }
        }
        });
       let p = {
	       guid: this.Guid,
	       deviceType: this.deviceType,
	       flag: 0,
	       searchDate :  this.dateNow,
	       pageNumber: 1,
	       pageSize: 20
	     };
       let that = this;
       that.getRequest({
        url: that.btnGroup[1].url,
        method: 'post',
        param: p,
        success: function (data) {
          that.btnGroup[1].num = data.total;
          },
          error: function () {
            that.btnGroup[1].num = 0;
          }
          })
       },
       windowChange: function () {

              let that = this;

              that.resizeCon();
              /*window.onresize = function () {
                 that.resizeCon();
              }*/
          },
      resizeCon: function () {
          let that = this;
          if ( that.$refs.myTable) {
            that.$refs.myTable.onResize();
          }
          if (that.showInfo) {
            let info = that.$refs.infoDetail;
            if (info && info.$refs.myTable){
              info.$refs.myTable.resizeTable(info.$refs.myOwnTable.width, info.$refs.myOwnTable.clientHeight - 50);
            }
          }
       },
      init: function () {
            let that = this;
            that.Guid = that.searchParam.guid = new GUID().newGUID();
            that.TableOpt.isSetExport = that.controls[6];
            that.TableOpt.queryParam.guid = that.Guid;
            that.TableOpt.queryParam.types =this.collectItem;
            that.TableOpt.queryParam.customerNoList = [];
            that.TableOpt.column = that.tblColumns;
            that.showTable = true;
            that.getHeight();
            that.$nextTick(function () {
              that.tableObj = that.$refs.myTable;
            });
            /*window.onresize = function () {

              that.windowChange();
            }*/

      },
        //获取采集指令
      getCollectItem: function (resVal) {
          this.collectItem = resVal.val;
          this.searchParam['types'] = this.collectItem;

      },
      comfimFunc:function(){
        //this.getParams(1);
        let arr = this.getSearchData(this.searchParam);
        this.$refs.myTable.resetTableOpt({column: this.tblColumns, isLoadData: false, queryParam: arr});
      },
    },
    created: function () {
      let that = this;
      that.btnGroup = [
            {name: "成功", num: 0, readStatus: 1, url: 'realTimeData/querySuccessList'},
            {name: "失败", num: 0, readStatus: -1, url: 'realTimeData/queryFailList'}
      ];
    },
    computed: {
      checkSelOption: function () {
        let arr = [{value: "0001ff00", text: '正向有功总', checked: true},
          //{value: "00900200", text: '剩余金额', checked: false},
           // {value: "05060101", text: '日冻结', checked: false},
           // {value: "00010001", text: '月冻结', checked: false}
         ];
        if(this.searchParam.deviceType !== '07') {
          arr = [{value: "0001ff00", text: '有功总示数', checked: true}
         ];
        }
        return {
          type: "checkSelect",
          cs: 'client',
          width: '14em',
          data: arr,
          key: 'collectItem'
        }
      },
      columns: function () {
        let baseColn = [
          {title: '用户姓名', field: 'customerName', textAlign: 'center', width: '130'},
          {
            title: '用户表号',
            field: 'deviceNo',
            textAlign: 'center',
            width: '170'
          },
          {
            title: '时间',
            field: 'collectTime',
            textAlign: 'center',
            width: '140'
          },
          {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '230'}];
        let selCollItem = this.collectItem.join(',');
        let appIndex = baseColn.length - 3;
        if (/0001ff00/gi.test(selCollItem)) {
          baseColn.splice(baseColn.length - 3, 0, {
            title: this.searchParam.deviceType != '07' ? '有功总示数' : '正向有功总', field: 'pr0', align: 'center', textAlign: 'center', width: '90', type: 'string',
            formatter: function (val, index, row) {
              let template = '';
              if (val !== null && val !== '' && typeof val !== 'undefined') {
                return '<span class="orangeSpan">' + val + '</span>'
              }
              return template
            }
          })
        }
        if (/00900200/gi.test(selCollItem)) {
          baseColn.splice(baseColn.length - 3, 0, {
            title: '剩余金额（元）', field: 'balance', textAlign: 'center', width: '160', type: 'string',
            formatter: function (val, index, row) {
              let template = '';
              if (val !== null && val !== '' && typeof val !== 'undefined') {
                return '<span class="orangeSpan">' + val + '</span>'
              }
              return template
            }
          })
        }
        if (/00010001/gi.test(selCollItem)) {
          baseColn.splice(baseColn.length - 3, 0, {title: '月冻结', field: 'monthFreezeP', textAlign: 'center', width: 120})
        }
        if (/05060101/gi.test(selCollItem)) {

          baseColn.splice(baseColn.length - 3, 0, {title: '上1日冻结', field: 'dayFreezeP', textAlign: 'center', width: 120})
        }
        let comm = [
          {title: '用户姓名', field: 'customerName', textAlign: 'center', width: '140'},
          {
            title: '用户表号',
            field: 'deviceNo',
            textAlign: 'center',
            width: '170'
          },
          {title: '联系电话', field: 'customerContact', textAlign: 'center', width: '120'},
          {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '230'},
        ];
        return [baseColn, comm]
      },
      deepSearchList() {
        let width = 'calc(100% - 6em)';
        if (this.dataReady) {
          return [
            {
              classes: 'halfWidth',
              type: 'input',
              isEmpty: true,
              label: '用户姓名',
              options: {
                width: width,
                type: 'normalInput',
                placeHolder: '可输入中文、数字',
                key: 'deepSearchParam.customerName',
                length: 15,
                value: '',
                regular: 'name'
              }
            },
            {
              classes: 'halfWidth hasMarginLi',
              type: 'input',
              isEmpty: true,
              label: '门牌编号',
              options: {
                width: width,
                type: 'normalInput',
                value: '',
                placeHolder: '可输入字母数字、下划线、-',
                key: 'deepSearchParam.propertyName',
                length: 30,
                limit: 'NCO'
              }
            },
            {
              classes: 'halfWidth ',
              type: 'input',
              isEmpty: true,
              label: '联系电话',
              options: {
                width: width,
                type: 'normalInput',
                value: '',
                placeHolder: '11位手机号',
                key: 'deepSearchParam.customerContact',
                errorMsg: '格式：11位手机号',
                limit: 'number',
                length: 13
              }
            },
            {
              classes: 'halfWidth hasMarginLi',
              type: 'input',
              isEmpty: true,
              label: '用户表号',
              options: {
                width: width,
                type: 'normalInput',
                placeHolder: '1-14位数字',
                value: '',
                key: 'deepSearchParam.deviceNo',
                regular: 'deviceNo',
                errorMsg: '格式：1-14数字',
                limit: 'number',
                length: 14
              }
            },
            {
              classes: 'halfWidth',
              type: 'input',
              isEmpty: true,
              label: '集中器号',
              options: {
                width: width,
                type: 'normalInput',
                placeHolder: '1-9位数字',
                key: 'deepSearchParam.jzqNo',
                value: '',
                errorMsg: '格式：1-14数字',
                limit: 'number',
                length: 9
              }
            },
            {
              classes: 'halfWidth hasMarginLi',
              type: 'input',
              isEmpty: true,
              label: '表计户号',
              options: {
                width: width,
                type: 'normalInput',
                placeHolder: '1-12位数字',
                value: '',
                key: 'deepSearchParam.meterUserNo',
                errorMsg: '格式：1-12数字',
                limit: 'number',
                length: 12
              }
            },
            {
              classes: 'allWidth',
              type: 'input',
              isEmpty: true,
              label: '用户地址',
              options: {
                width: width,
                type: 'normalInput',
                value: '',
                placeHolder: '50个字符内',
                key: 'deepSearchParam.customerAddr',
                length: 50,
                regular: 'address'
              }
            },
          ]
        }
      },
      FormOpt() {
        let line = this.getEnumItem('isOnline').obj;
        line.unshift({text: '全部状态', value: ''});
        let arr = [
          {text: '用户地址', value: 'customerAddr'},
          {text: '门牌编号', value: 'propertyName'},
          {text: '集中器号', value: 'jzqNo'},
          {text: '用户姓名', value: 'customerName'},
          {text: '联系电话', value: 'customerContact'},
          {text: '表计户号', value: 'meterUserNo'},
          {text: '用户表号', value: 'deviceNo'}
        ];
        let arrs = this.getEnumItem('deviceType').obj;
       // if(this.$store.state.currentSearchType == 1) {
          // arrs = [{text: '电表', value: '07'}]
       // }
        return [
          {
            type: 'select',
            options: {
              cs: 'client',
              width: '8.3em',
              data: arrs,
              text: 'text',
              value: 'value',
              key: 'searchParam.deviceType'
            }
          },
          {
            type: 'select',
            options: {
              cs: 'client',
              width: '8em',
              data: line,
              text: 'text',
              value: 'value',
              key: 'searchParam.isOnline'
            }
          },
          {
            type: 'input',
            options: {
              type: 'typeSearch',
              key: 'searchParam',
              selectOpt: {
                width: '7.5em',
                cs: 'client',
                data: arr,
                key: 'searchType'
              }
            }
          }
        ]
      },
      deviceType() {
        return this.searchParam.deviceType
      },
      tblColumns: function () {

        let baseColn = [
          {field: 'state', type: 'checkBox', textAlign: 'center', width: 40},
          {
            title: '状态',
            field: 'isOnline',
            textAlign: 'center',
            width: 120,
            type: 'string',
            formatter: function (val, index, row, obj) {

              let template;
              switch (row.readStatus) {
                case 0 :
                  if (val == '0') {
                    template = '<span class="orange">离线</span>'
                  } else {
                    template = '<span class="green">在线</span>'
                  }
                  break;

                case 1:
                  if (val == '0') {
                    template = '<span class="green">召测成功</span>'
                  } else {
                    template = '<span class="green">召测成功</span>'
                  }
                  break;
                case -1:
                  if (val == '0') {
                    template = '<span class="red">召测失败</span>'
                  } else {
                    template = '<span class="red">召测失败</span>'
                  }
                  break;
                case 3:
                  if (val == '0') {
                    template = '<span style="color:sandybrown">正在召测</span>'
                  } else {
                    template = '<span style="color:sandybrown">正在召测</span>'
                  }
                  break;
              }

                return template;
            }
          },
          {title: '用户姓名', field: 'customerName', textAlign: 'center', width: 180},

          {
            title: '用户表号',
            field: 'deviceNo',
            textAlign: 'center',
            width: 160
          },


          {
            title: '时间',
            field: 'collectTime',
            textAlign: 'center',
            width: 160
          },
          {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: 120},
          {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: 200},
        ];
        let selCollItem = this.collectItem.join(',');
        let appIndex = baseColn.length - 3;

        if (/0001ff00/gi.test(selCollItem)) {
          baseColn.splice(appIndex, 0, {
            title:  this.searchParam.deviceType == '07' ? '正向有功总' : '有功总示数',
            field: 'pr0',
            align: 'center',
            textAlign: 'center',
            width: 100
          })
        }
        if (/00900200/gi.test(selCollItem)) {
          baseColn.splice(appIndex, 0, {title: '剩余金额（元）', field: 'balance', textAlign: 'center', width: 120})
        }
        if (/00010001/gi.test(selCollItem)) {
          baseColn.splice(appIndex, 0, {title: '月冻结', field: 'monthFreezeP', textAlign: 'center', width: 120})
        }
        if (/05060101/gi.test(selCollItem)) {
          baseColn.splice(appIndex, 0, {title: '上1日冻结', field: 'dayFreezeP', textAlign: 'center', width: 120})
        }

        return baseColn;
      }
    },
    mounted: function () {
      let that = this;
      that.isIn();
      that.getAction();
    },
    watch: {
      'searchParam.isOnline': {
        handler(val) {
          this.getParams(1)
        }
      },
      'searchParam.deviceType': {
        handler(val, oldVal) {
          if (val !== oldVal) {
            //this.$refs['inputs'][0].search = '';
            this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'guid', 'isOnline', 'customerNoList', 'deviceNoList', 'isSearchChild']);
            this.getParams(1)
          }
        }
      },
      '$store.state.currentSearchType': {
          handler(val, oldVal) {
            if(val !== oldVal) {
              if(val === 0) {
                //this.searchParam.deviceNo = '';
               // this.searchParam.customerNo = this.$store.state.selectedUser;
              } else {
               // this.searchParam.customerNo = '';
               // this.searchParam.deviceNo = this.$store.state.selectedNode;
              }

              //this.getParams(1)
            }
          }
        },
      $route(to, from) {
       // this.closeDiv();
      }
    }
  }
</script>
<style lang="scss">
  .right-container .center-container .noback-Container{
    background: transparent;
    padding:0;
    width:100%;
  }
</style>



