<template>
    <div >
    	<ul class = "myTree" >
    	  <li v-for ="(node, $index) in treeData" :class="{'hasChild': true}">
          <span class="treeSpan" :class="{treeSpanNoCheck: singleSelect || (noCheck instanceof Object && node[noCheck.key] == noCheck.value)}">
            <span  v-if="$index === 0 && node.hasChild &&  !isInit" class="fistChild"></span>
            <span class="closeChild" 
             :class="{
              openChild: node.open, 
              leafChild: !node.hasChild, 
              lastLeafChild: $index === treeData.length -1 && !node.hasChild,
            }" 
             @click.stop="toggleChild(node, $index)"></span>
            <span  :class="{hasBrotherLBL:true, noBrotherLBL:!brotherHasC, firstBrotherLBL: ($index == 0&& brotherHasC) || ($index == treeData.length -1 && node.hasChild) }"></span> 
            <span v-if="!(singleSelect || (noCheck instanceof Object && node[noCheck.key] == noCheck.value))" class="sekectCheckbox" :class="{selectedCheckbox: node.selected,  selectedCheckboxC:node.hasChild && node.selected,  hasBrotherL: brotherHasC }" @click.stop="selectNode([node, $index, true])"></span> 
            <span class="showName" :class="{selectedSpan: node.selected && !(noCheck instanceof Object && node[noCheck.key] == noCheck.value)}" @click.stop="singleSelect ? getSingleNode(node) : !(singleSelect || (noCheck instanceof Object && node[noCheck.key] == noCheck.value)) && selectNode([node, $index, true])" v-html="getHeightTip(node)">
                   <!-- {{node.nodeName}} -->
             </span>
          </span>
           <span  v-if="!node.hasChild && !node.open && $index < treeData.length -1 "  class="connectBrother connectBrotherNoCT"></span>
           <span  v-if="!node.hasChild && !node.open && $index == treeData.length -1 "  class="connectBrother connectBrotherNoCBL"></span>
           <span  v-if="node.hasChild && !node.open && $index < treeData.length -1 "  class="connectBrother connectBrotherNoCB"></span>
    	  	<template v-if="node.hasChild && node.children && node.children.length>0" >
             <span  v-if="node.open && treeData.length !==1 &&  $index < treeData.length -1 " class="connectBrother" ></span>
    	  	 	 <Trees :treeOpt="treeOpt"  :noCheck="noCheck" :childData="node.children" :isInit= "false"  v-show="node.open" @getNode="selectNode" :singleSelect="singleSelect" @getSingle="getSingleNode"></Trees>
    	  	 </template>
         
    	  </li>
    	</ul>
      <dialogs type="alert" ref="alerts"></dialogs>
    </div>
</template>
<script>
  let nodeSelect = [];
   export default{
   	 name: 'Trees',
   	 data() {
   	 	return {
         treeData: [],
         travelAll: false,
         isChecked: false
   	 	}
   	 },
     computed: {
       brotherHasC: function () {
           for(let i =0 ;i<this.treeData.length; i++) {
             if(this.treeData[i].hasChild ) {
                return true
             }
           }
           return false
        }
     },
   	 props: ['treeOpt', 'isInit', 'childData', 'singleSelect',  'noCheck', 'hasInitSelect'],
     methods: {
        getHeightTip: function (node) {
          let name = node['nodeName'];
          for(let i = 0; i < this.treeOpt.searchVal.length; i++) {
            let key = this.treeOpt.searchVal[i];
            if(key !== '' && key !== null && name.indexOf(key) !== -1) {
               // return true
               let arr = name.split(key);
               name = arr.join('<span class="hasSearchVal">' + key + '</span>')
            }
          }
         // return false；
           return name
        },

        getSingleNode: function(node) {
          if(this.isInit) {
            this.getAllSelect(false, true);  
            let arr = this.removeChild(node);
            this.$emit('getSingle', arr);    
          }
          node.selected  = true;
          if(!this.isInit) {
            this.$emit('getSingle', node);
          }
          
        },
        toggleChild: function (node, index) {
          this.treeData[index].open = !node.open;
          if(node.hasChild && (node.children && node.children.length === 0) || typeof node.children == 'undefined') {
             let para = {nodeId: node.nodeId};
            for(let key in this.treeOpt.param) {
              para[key] = this.treeOpt.param[key]
            }
            this.getTreeData(para, index);
           } 
        },
        traverseTree: function (child, isCheck, signs) {

           for(let i=0; i<child.length; i++) {
              child[i].selected = isCheck;
              let canSelect = !(this.noCheck instanceof Object && child[i][this.noCheck.key] == this.noCheck.value);
                if(nodeSelect.length === 0 && isCheck) {
                  if(canSelect) {
                       nodeSelect.push(this.removeChild(child[i]));
                  }
               
                } else {
                   for(let j=0; j<nodeSelect.length; j++) {
                      if(nodeSelect[j].nodeId == child[i].nodeId) {
                          if(!isCheck) {
                             nodeSelect.splice(j, 1);
                          }
                         break;
                       } 
                       if(j === nodeSelect.length - 1) {
                          if(isCheck  && canSelect ) {
                              nodeSelect.push(this.removeChild(child[i]));
                            }
                        break;
                    }
                 } 
               }
               
              if(child[i].children && child[i].children.length > 0 && (child[i].open || signs)){
                   this.traverseTree(child[i].children, isCheck, signs)
               }
             }
        },
        //除去当前节点的子节点数据
        removeChild: function (obj) {
            let current = {};
            for(let item in obj) {
              if(item !== 'children') {
                current[item] = obj[item]
              }
            }
            return current
        },
        // val = [node. index, 当前标签的标识],是否全选
        selectNode: function (val, signs) {

           if(typeof val[0] == 'undefined') {
            this.$emit('getNode', []);
             return 
           }
           let canSelect = !(this.noCheck instanceof Object && val[0][this.noCheck.key] == this.noCheck.value);
           if(val.length === 3 && typeof val[2] == 'boolean' && val[2]){
             let selected = signs ? this.isChecked : !val[0].selected;
             let sign =  this.treeData[val[1]].selected == selected;
             this.treeData[val[1]].selected = selected;
             if(nodeSelect.length === 0 && val[0].selected){
                if(canSelect) {
                   nodeSelect.push(this.removeChild(val[0]));
                }
               // nodeSelect.push(val[0].nodeId);
             } else {
                  for(let j=0; j<nodeSelect.length; j++) {
                    if(nodeSelect[j].nodeId == val[0].nodeId) {
                       if(!sign ) {
                          nodeSelect.splice(j, 1);
                       }
                       break;
                    } else if(j == nodeSelect.length - 1 && val[0].selected) {
                        if(canSelect) {
                           nodeSelect.push(this.removeChild(val[0]));
                        }
                       // nodeSelect.push(val[0].nodeId);
                        break;  
                    }
                  }
             }
             if (val[0].hasChild && val[0].children &&  val[0].children.length > 0 && (val[0].open || signs) ) {
               this.traverseTree(val[0].children, val[0].selected, signs);
             }
             this.$emit('getNode', nodeSelect);
             return
           } 

           if(val.length === 3 && typeof val[2] == 'boolean' && val[2]){
              val[2] = false;
              this.$emit('getNode', val); 
           } else {
              this.$emit('getNode', nodeSelect);
           }
        },
        getTreeData: function (param, index) {
          let that = this;
          if(that.treeData.length === 0 || (that.treeData[index].hasChild && (typeof that.treeData[index].children === 'undefined' || that.treeData[index].children.length == 0))){
              that.getRequest({
                url: typeof index !== 'undefined' ? (that.treeOpt.childUrl ? that.treeOpt.childUrl : that.treeOpt.url) :  that.treeOpt.url,
                method: that.treeOpt.method || 'post',
                param: (typeof index !== 'undefined') ? param : (that.treeOpt.param || {}),
                success: function (data) {
                  let Data = data.data;
                 
                  if(typeof index !== 'undefined') {
                    // if(Data.length == 0) {
                    //   if(that.treeOpt.param.isImportant == 1) {
                    //     that.$refs.alerts.show({
                    //       msg: '该总表下没有重点用户'
                    //     })
                    //   }
                    // }
                    that.$set(that.treeData[index], 'children', Data)
                    //that.treeData[index].children = Data;

                  } else {
                    if(Data.length == 0) {
                       that.$emit('emptyData')
                    } else {
                       for(let j=0; j<Data.length; j++) {
                           Data[j].isTop = true;
                       }
                    }
                    that.treeData = Data;
                    if(that.hasInitSelect && Data.length > 0) {
                      that.treeData[0].selected = true;
                      that.$emit('getInitSelect', that.removeChild(that.treeData[0]));
                    }
                    if(typeof data.total !== 'undefined') {
          
                      that.$emit('getTotal', data.total)
                    }
                   
                  }
                },
                error: function (data) {
                     that.$refs.alerts.show({
                          msg: data.message
                      })
                }
               //, (that.treeOpt.queryType === 'form' || typeof index !== 'undefined') ? true : false
             })
          }
        },
        getAllSelect: function (isChecked, allSelect) {
          this.travelAll = true;
          this.isChecked = isChecked;
          nodeSelect = [];
          this.$emit('getNode', nodeSelect);
          for(let i=0; i<this.treeData.length; i++) {
             this.selectNode([this.treeData[i],i, true], allSelect)
           }
        },
     },
   	 mounted: function () {
   	    this.isInit ? this.getTreeData() : this.treeData = this.childData;
        this.isInit ? nodeSelect = [] : nodeSelect = nodeSelect
   	 }
   }
</script>
  
<style lang="scss">
  ul.myTree {
  	margin: 0;
  	padding: 0;
    width: 100%; 
    min-width: 14em;
    height: 100%;
    color: inherit;
    .treeSpan{
      display: block;
      width: calc(100% - 3em);
      text-align: left;
      padding-left: 3em;
      color: inherit;
      height: 2.5em;
      line-height: 2.7;
    }
    .treeSpanNoCheck{
       width: calc(100% - 1.4em);
       padding-left: 1.4em;
    }
    li {
      position: relative;
      padding-left: 1.4em;
      width: calc(100% - 1.4em);
      ul {
    
        width: fit-content;
        li {
          margin-left:0px;
          width: 100%
        }
      }
    }
  
    li.hasChild {
      .showName{
        cursor: pointer;
        white-space: nowrap;
      }
      .closeChild{
        
         width: 1.7em;
         height: 1.75em;
         position: absolute;
         left: 0em;
         background-image: url(../../assets/img/tree/closeF2.png);
         background-repeat: no-repeat;
        
         top: 4px;
         cursor: pointer;
         background-size: contain
       }
       
       .sekectCheckbox{
          top: 6px;
          cursor: pointer;
          position: absolute;
          width: 1.65em;
          height: 1.65em;
          left:2.2em;
          background: url(../../assets/img/tree/check_empty.png);
          background-position: center;
          background-repeat:no-repeat;  
           background-size: contain;
       }
       .selectedCheckbox{
         background-image: url(../../assets/img/tree/check.png);
       }
       .sekectCheckboxC{
          background: url(../../assets/img/tree/sx.png) 12.3px 24px, url(../../assets/img/tree/check_empty.png);
          background-repeat:no-repeat;
          height: 30px
       }
       selectedCheckboxC{
          background: url(../../assets/img/tree/sx.png) 11.5px 24px, url(../../assets/img/tree/check.png);
          background-repeat:no-repeat;
          height: 30px
       }
    
       .fistChild{
          height:1em;
          top:-0.6em;
          position:absolute;
          left: calc((1.75em - 1px) / 2);
          width:1px;
          background: url(../../assets/img/tree/sx.png);
       }
      .leafChild + .sekectCheckbox  +span.showName{
         padding-left:0.3em;
       }
      .openChild{
         background-image: url(../../assets/img/tree/fold_up_right_down1.png);
         background-size: contain;
       }
   
      .leafChild{
        background-image: none
       }
       .hasBrotherL{
         left: 2.8em
       }
      .hasBrotherLBL{
        width: 1.5em;
        height: 1px;
        top: 1.2em;
        left: 1.05em;
        position:absolute;
        background-image: url('../../assets/img/tree/hx.png') ;
        background-position: left;
        background-repeat: repeat-x;
        background-size: contain
      }
      .noBrotherLBL{
          width: 1em;
       }
      .firstBrotherLBL{
          width: 0.9em;
          left: 1.75em;
       }
       .hasBrotherLBL  + .hasBrotherL +span.showName{
         padding-left:0.4em;
       }
      
      .lastLeafChild{
        
         background-image: none;
         background-repeat: no-repeat;
         top:5px;
         height: 11px;
         width: 16px;
         background-size:contain;
      }
     
      .connectBrother{
        background: url(../../assets/img/tree/sx.png);
        position: absolute;
        left: 0.84em;
        width: 1px;
        top:2.1em;
        height:calc(100% - 1.7em);
        background-position: 0 0;
        background-repeat: repeat-y;
        background-size: contain
       }  
       .connectBrotherNoCB{
        
           height: calc(100% - 1.7em);
           top: 2.1em;
       }
       .connectBrotherNoCBL{
          
           top:0;
           height: 1.3em;
        }
       .connectBrotherNoCT{
       
         height: 100%; 
         top: 0px;
         background-size: contain;
       }
     
       .hasSearchVal{
         color: #C43829;
       }
      .selectedSpan{
          color: rgb(251, 214, 63);
          .hasSearchVal{
            color: rgb(251, 214, 63)!important;
          }
       }
      }
  }
 @media screen and (max-width: 1366px) {
     
     
    
  }
  @media screen and (min-width: 1920px){
    
    
  }
</style>


