<template>
  <div class="info-div"  style="margin-top:1em;height:calc(100% - 1em)">
    <div class="info-header">
      <span class="callback" @click="resetComponent"><!-- {{type === '1'? '实时数据' : '历史数据'}} --> 普通用户 </span>
      <span> > 数据详情</span>
    </div>
    <div class="info-container" v-if="show" style="margin-top:0;height:100%">
     
      <div class=" list-content all_com_left list-contentB" id="all_com_left" :class="{all_com_left_allW: deviceTypes != '07'}">
        <ul class="all_com_right_ul">
          <li style="margin-top: 0.5em; height: 1.4em;">
            <i class="hasIcon hasImg">
              <img src="dist/img/measure/basic-info.png" />
            </i>
            <span style="margin-left:0.5em">基础信息</span>
          </li>
          <li v-for="(item, $index) in customerInfo" v-if="item.show" class="centerLi">
            <span class="label-show">{{item.label}}：&nbsp;&nbsp;</span>
            <span class="label-show" style="text-align: left">{{item.value}}</span>
          </li>
        </ul>
      </div>
       <div class=" list-content all_com_right list-contentB" style="margin-top:0" v-if="deviceTypes == '07'">
          <div class="list-header" style="margin-bottom:0">
           <i class="hasIcon hasImg">
                <img src="dist/img/measure/charts.png" />
           </i>采集数据统计
         </div>
         <div class="analyseDiv">
             <div class="analyseLeft">
                 <p class="HF-p">
                    <span>账户余额</span>
                      <i class="hasIcon hasImg"  @click="getAnalyse(true)" style="margin-right:0" >
                          <img src="dist/img/costControl/fresh.png" style="height:auto" />
                      </i>
                 </p>
                 <div class="contentDiv">
                     <div>
                       <p class="bigNum">
                          <i class="hasIcon hasImg">
                            <img src="dist/img/measure/money.png"  />
                          </i>
                          <span :title="analyseData.balance">
                             {{analyseData.balance}}
                          </span>
                       </p>
                       <span class="little-span">
                         购{{optObj.name}}金额：  <font style="color:#F7C73A">{{analyseData.payMoney}} &nbsp;&nbsp;&nbsp;&nbsp;</font>
                         购{{optObj.name}}次数：  <font style="color:#F7C73A">{{analyseData.payCount}} </font>
                       </span>
                       <span class="little-span">截止时间： <font style="color:#F7C73A">{{analyseData.lastFlushTime}} </font></span>
                     </div>
                 </div>
             </div>
             <div class="analyseRight">
                <div class="analyseLDiv">
                    <div class="leftSpan">
                        <img src="dist/img/measure/elect_thismonth.png"  />
                    </div>
                     <div class="rightSpan">
                        <span class="leftSpanEven" :title="analyseData.currentMonthElect">
                           {{analyseData.currentMonthElect}}
                        </span>
                         <span > 当月电量</span>
                    </div>
                 </div >
                 <div  class="analyseLDiv">
                    <div class="leftSpan"> 
                        <img src="dist/img/measure/elect_lastmonth.png"  />
                    </div>
                    <div class="rightSpan">
                        <span class="leftSpanOdd" :title="analyseData.lastMonthElect">
                           {{analyseData.lastMonthElect}}
                        </span>
                         <span > 上月电量</span>
                    </div>
                  </div>
                 
                 <div class="splitDiv"></div>
                  <div class="analyseLDiv">
                    <div class="leftSpan">
                        <img src="dist/img/measure/elect_today.png"  />
                    </div>
                     <div class="rightSpan">
                        <span class="leftSpanEven" :title="analyseData.currentDayElect">
                           {{analyseData.currentDayElect}}
                        </span>
                         <span > 今日电量</span>
                    </div>
                 </div>
                  <div class="analyseLDiv">
                    <div class="leftSpan">
                        <img src="dist/img/measure/elect_yesterday.png"  />
                    </div>
                     <div class="rightSpan">
                        <span class="leftSpanOdd" :title="analyseData.lastDayElect">
                           {{analyseData.lastDayElect}}
                        </span>
                         <span > 昨日电量</span>
                    </div>
                 </div>
                 
             </div>
         </div>
      </div>
      <div class=" list-content all_foot list-contentB" >
         <div class="list-header">
           <i class="hasIcon hasImg">
                <img src="dist/img/measure/data-list.png" />
           </i>采集数据列表
        </div>
       <div class="operate-div">
         <template v-for="(item, $index) in FormOpt">
           <selects ref="selects" v-if="item.type == 'select'" :option="item.options" @getSelect="getOption" ></selects>
           <datePicker ref="timer"  v-if="item.type == 'timer'" type='doubleDate'
            :dateOpt="timeOpt" @getDatePicker="getOption" ></datePicker>
          </template>
        </div>
        <div class="all_foot_cons" style="margin-top:0.4em;position:relative" ref="myOwnTable">
              <tables ref="myTable" v-if="showTable" :tableOpt="TableOpt" class="smallPadding" ></tables>
        </div>

       
      </div>
    </div>
    <dialogs type="alert" ref="alerts"></dialogs>
      </div>
</template>
<script>
import 'vue-awesome/icons/download'
  export default {
    data () {
      return {
        showTable: false,
        showLine: true,
        choseMonth: 1,  //点击折线图，获取点击月份
        legendSelected: '电',
        tabIndex: 0,
        lineChart: '',
        show: true,
        MonthSelect: false,
        userId: '',
        calcDate:"",//计算日期
        customerNo: "",
        tableData: [],
        monthDate: '', //存储月份初始值
        tableDataForMonth: '',
        optObj: {
          name: '电', dw: '度', val: '07', chooseSpan: 0
        },
        btns: [
          {name: '电', dw: '度', val: '07', chooseSpan: 0, img: 'dist/img/face/elec.png',  key: 'elData'},
          {name: '水', dw: '吨', val: '08', chooseSpan: 1, img: 'dist/img/face/water.png', key: 'waterData'},
          {name: '气', dw: 'm³', val: '09', chooseSpan: 2, img: 'dist/img/face/gas.png', key: 'gasData'}
        ],
        TableOpt:  {
                 column: [],
                 rowName: 'data',
                 queryParam: {deviceType: '07'},
                 url: 'realTimeData/queryListDetails',
                 method: 'post',
                 pageSize: 20,
                 pageNumber: 1,
                 pageList: [20,40,60],
                 exportURL: 'realTimeData/downUserDetail',
                 isExport: true,
                // isSetExport: true
         },
        userInfo: '',
        ratio: 1,
        searchParam: {
          customerNo: '',
          meterUserNo: '',
          startDate: '',
          endDate: '',
          dataType: '',
          deviceType: '07'
        },
        searchDate: '',
        analyseData: {
          currentMonthElect: '--',
          lastMonthElect: '--',
          currentDayElect: '--',
          lastDayElect: '--',
          balance:'--',
          lastFlushTime: '--',
          payMoney: '--',
          payCount: '--'
        }
      }
    },
    //type: type === '1'? '实时数据' : '历史数据'
    props: ['row', 'type', 'deviceTypes'],
    methods: {
        
        resetComponent: function () {
            let that = this;
            that.show = false;
            that.$emit('closeDiv');
            setTimeout(function () {
                that.show = false;
            }, 0)
        },
        downLoads: function () {
         let data = {
                 customerNo: this.customerNo,
                  yearMonth: this.tableDataForMonth,
                  deviceCno: this.row.deviceCno
          };
         this.downLoad({
            url: 'historyData/downUserDetail',
            data: data
         })
      },
        getOptions: function (val) {
         if(val.val !==  this[val.key])  {
            this[val.key] = val.val;
            this.getUseDataForMonth()
         }
       
       },
     
      getAnalyse: function (flag) {
         let that = this;
         that.getRequest({
           url: 'realTimeData/summaryData',
           method: 'post',
           param: {customerNo: this.customerNo, deviceCno: that.row.deviceCno},
            success: function (data) {
                let Data = data.data;
                for(let key in Data) {
                  that.analyseData[key] = Data[key]
              }
              if(flag) {
                 that.$refs.alerts.show({msg: '刷新成功！'})
              }
            }
         }, true)
      },
      //请求客户的详细信息
      getUserInfo: function () {
        let that = this;
        this.getRequest({
            url: 'historyData/queryDetails',
            method: 'post',

            param: {customerNo: this.customerNo, deviceCno: that.row.deviceCno},
            success: function (data) {
                that.userInfo = data.data;//客户信息
                that.ratio = data.data.ratio
            }
        }, true);
      },
      
      getType: function () {
          let that = this;
          let param = {
            '电': false,
            '水': false,
            '气': false
          } ;
          that.legendSelected = params.name;
          param[params.name] = true;
          that.lineChart.setOption({
            legend: {
                selected: param
            }
          })
      },
   
      //根据客户和月份请求每天的水、电、气的使用量
      getUseDataForMonth: function () {
        let that = this;
        let param = this.getSearchData(that.searchParam);
        if(that.showTable) {
            that.$refs.myTable.resetTableOpt({column: that.column, queryParam: param})
        } else {
            that.TableOpt.column = that.column;
            that.TableOpt.queryParam = param;
            that.showTable = true;
        } 
      },
     
      init: function () {
            this.userId = sessionStorage.userId;
            if (this.userId === undefined || this.userId === '') {
                this.$router.push({path: '/'})
            } else {
                this.customerNo = this.row.customerNo;
                this.searchParam.customerNo = this.row.customerNo;
                this.searchParam.meterUserNo = this.row.meterUserNo;
                this.searchParam.deviceType = this.deviceTypes;
                let date = this.initDate();
                this.searchDate = date.start + '至' + date.end;
                this.getUserInfo();
                this.getAnalyse();
            }

        }
    },
    computed: {
        column: function () {
           return  [
                            {title: '采集时间', field: 'collectDate', textAlign: 'center', width: 180},
                            {
                                title: '表号',
                                field: 'deviceNo',
                                textAlign: 'center',
                                width: 160
                            },
                            {title: this.deviceTypes == '07' ? '正向有功总' : '有功总示数', field: 'pr0', align: 'center', textAlign: 'center',  width: 100},
                            {title: '账户余额', unShow: this.deviceTypes != '07', field: 'balance', textAlign: 'center',  width: 120},
                            // {title: '购'+this.optObj.name+'金额', field: 'payMoney', textAlign: 'center',  width: 140},
                            // {title: '购'+this.optObj.name+'次数', field: 'payCount', textAlign: 'center',  width: 140},
                            {title: '数据类型', field: 'dataType', textAlign: 'center',  width: 140,type: 'string',
                                   formatter: function (val, index, row, obj) {
                                       return  '<span>' + obj.$parent.$parent.getEnumItem('isRealTime', val).text + '</span>'
                                   }
                            }
                   ]
        },
        timeOpt: function () {
            let dates = this.initDate();
            return {
              key: 'searchDate',
              value: {startDate: dates.start, endDate: dates.end},
              width: '23em',
              fontSize: '0.8em'
            }
          },
        FormOpt() {
            return [
              {
                
                type: 'select',
                options: {
                   width: '7.5em',
                   cs: 'client',
                   data: this.getEnumItem('isRealTime').obj,
                    key: 'searchParam.dataType'
                }
              },
              {
                type: 'timer',
              }
            ]
          },
       customerInfo: function () {
      
            if (this.userInfo !== '') {
              let commBaudrateT = '--';
              switch(this.userInfo.commBaudrate) {
                case 1:
                 commBaudrateT = 600;
                 break;
                case 2:
                 commBaudrateT = 1200;
                 break;
                case 3:
                 commBaudrateT = 2400;
                 break;
                case 4:
                 commBaudrateT = 4800;
                 break;
                case 5:
                 commBaudrateT = 7200;
                 break;
                case 6:
                 commBaudrateT = 9600;
                 break;
                case 7:
                 commBaudrateT = 19200;
                 break;
                case 0:
                  commBaudrateT = 3;
                  break;
              }
              return [
                {label: '用户姓名', show: true, value: this.userInfo.customerName || '--'},
                {label: '联系电话', show: true,value: this.userInfo.customerContact|| '--'},
                {label: '用户地址', show: true,value: this.userInfo.customerAddr|| '--'},
                {label: '表计倍率', show: this.deviceTypes == '07',  value: this.userInfo.ratio},
                {label: '用户表号', show: true,value: this.userInfo.deviceNo|| '--'},
                {label: '采集器号', show: true,value: this.userInfo.cjqNo|| '--'},
                {label: '集中器号', show: true,value: this.userInfo.jzqNo|| '--'},
                {label: '测量点号', show: true,value: this.userInfo.commPointCode|| '--'},
                {label: '波特率', show: this.deviceTypes == '07',value: commBaudrateT},

              ]
            }
       }
    },
    components: {
    },
    mounted: function () {
        this.init();
    },
    watch: {
      'searchDate': {
        handler(val) {
           let arr = val.split('至');
           this.searchParam.startDate = arr[0];
           this.searchParam.endDate = arr[1];
           this.getUseDataForMonth()
        }
      },
      'searchParam.dataType': {
        handler(val) {
          
           this.getUseDataForMonth()
        }
      }
    }

  }
</script>

<style scoped lang="scss">
  @import '../../assets/main.scss';
  $infoW: 55%;
  $infoH: 13em;
  $infoHM: 8em;
  .all_foot_btn span.selected {
    background: #02A4EA;
    color: white
  }
  .all_com_right_ul{
    height: 100%
  }
  .all_com_right_ul li {
    width: calc(100% - 4em);
    height: calc((100% - 2em) / 7);
    list-style-type: none;
    padding: 0.2em 0;
    font-size: 0.83em
  }
  .all_com_right_ul li span{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: block;
    float: left;
    margin:0;
    max-width: calc(100% - 4.4em);
  }
  .all_com_right_ul li.centerLi{
    width: calc(50%);
    float:left;
    .label-show{
      width: 40%;
    }
    .label-show + .label-show{
      width: calc(60% - 2em);
      
    }
  }
  span.bigShow{
    font-size:3.5em
  }
  .infos {
    background-color: #f5f5f5;
    /*background-color: black;*/
