    padding: 0;
    font-size: 0.7em;
    position: relative;
    height: 100%;
    overflow: hidden;
  }

  .all_head {
    width: 100%;
    height: 60px;
    line-height: 60px;
    background-color: white;
    float: left;
    margin-bottom: 15px;
  }

  .all_com_left {
    width: calc(100% -  #{$infoW} - 0.5em);
    height: $infoH;
    float: left;
    margin-left:0;
    margin-top:0!important;
    padding:0.5% 0 !important;
  }
  .all_com_left_allW{
    width:100%;
    height:$infoHM;
     .all_com_right_ul li.centerLi{
        width: 25%;
        height: calc((100% - 2em) / 4)
     }
   }
  .all_foot_ta {
    width: 100%;
    text-align: center;
    line-height: 1.4em;
    overflow: scroll;
    height: 1.4em;
    margin-bottom:0;
    margin-left: 0;
  }
  .all_foot_ta td {
    padding: 0.3em 0;
    border-right: 1px solid #7B8390;
    font-size:0.9em;
  }
  .all_foot_ta td:last-child{
    border-right:0
  }
  .all_foot_ta tr{
    border-bottom:1px solid lightgray;
    padding: 5px 0
  }
  .echarts {
    width: 100%;
    height: 100%;
  }
  .all_com_right {
    width: $infoW;
    height: $infoH;
    background-color: rgba(#305ea0,0.3);
    float: left;
    padding: 0.5% 0 !important;
    margin-left:0.5em;
    .analyseDiv{
      width:calc(100% - 2px - 3.2em);
      height: calc(100% - 2em - 1.8em);
      display: inline-block;
      padding: 0em 1.6em 1em 1.6em;
      .analyseLeft{
        width: calc(40% - 1.2em);
        border:0.2em solid #3E8BDB;
        height:calc(100% - 1.2em);
        padding:0.6em;
        float:left;
        border-radius:0.5em;
         p.HF-p{
            font-size: 0.94em;
            text-align:left;
            margin:0;
            i.hasIcon{
              float:right;
              color: #3E8BDB;
              margin-right: 0.5em;
              cursor: pointer;
            }
            i.hasIcon:hover{
              color: white
            }
          }
          .contentDiv{
             margin: auto;
             text-align: center;
             height: calc(100% - 1em);
             width: 100%;
             display: table;
             div{
               display: table-cell;
               vertical-align:middle;

               .hasImg{
                  width:2.12em;
                  height:2.12em;
                  margin-top:0.2em
                }
               span{
                 display:block;
                 max-width: 7em;
                 font-size: 1.6em;
                 white-space: nowrap;
                 text-overflow: ellipsis;
                 overflow: hidden;
                 float:left;
                 color: #EE8836;
                 text-align:center;
                 font-weight: 600;
                 line-height:1.6
               }
               span.little-span{
                 font-size: 0.83em;
                 display:block;
                 width:100%;
                 max-width:100%;
                 text-align:center;
                 font-weight: 100;
                 color:white;
                 margin-top: 0.5em;
              }
              .bigNum{
                margin:0 auto;
                width: -webkit-max-content;
                width: -moz-max-content;
                width: max-content; 
              }
              .bigNum:after{
                @extend .clearElement
               }
            }
           
          }
          .contentDiv:after{
             @extend .clearElement
           }
      }
      .analyseLeft:after{
        @extend .clearElement
      }
      .analyseRight{
         height:100%;
         float:left;
         width:calc(60% - 10px - 0.2em - 4em);
         padding:0 2em;
         .analyseLDiv{
           width: calc(50% - 2px - 1em);
           padding: 0 0.5em 0 0.5em;
           height: 50%;
           float:left;
           position:relative;
          .leftSpan{
             height:100%;
             width:2em;
             margin-left:0.5em;
             float:left;
               img{
                 width: 2.45em;
                 height:2.45em;
                 margin-top:65%;
               }
           }
        
          .rightSpan{  
             float: left;
             height: calc(100% - 1em);
             padding-top: 0.8em;
             max-width: calc(100% - 3em);
             text-align: center;
             min-width: 6.5em;
            span{
              display:block;
              width:100%;
              line-height:1.3;
              font-size:0.87em;
            }
            span.leftSpanOdd{
              color: #139AF8;
              font-weight:600;
              font-size:1.4em;
            }
            span.leftSpanEven{
              color: #F6C838;
              font-weight:600;
              font-size:1.4em;
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
            }
          }
          
         }
         .splitDiv{
           width:100%;
           background: rgba(#fff, 0.4);
           height:1px;
           margin:0.3em 0 ;
           float:left;
         }

      }
    }
  }

  .all_foot {
    width: 100%;
    //background-color:rgba(255,255,255,0.1);
    float: left;
    height: calc(100% - #{$infoH} - 4em);
    margin-top:1em
  }
  .all_foot,.all_com_right{
    .list-header{
      border-bottom:0;
      padding-left: 2em;
      width: -webkit-fit-content;
      width: -moz-fit-content;
       width: fit-content;  
       float: left;
    }
    .operate-div{
      padding:0 1.3em 0 0;
      font-size:0.94em
    }
  }

  .all_com_left_allW + .all_foot{
      height: calc(100% - #{$infoHM} - 4.5em);
  }
  .all_foot_cons {
    //border: 1px solid silver;
    /*border: 1px solid #384850;*/
    border-radius: 2px;
    margin-left: 1.2%;
    position: relative;
    float:left;
    margin-bottom: 0;
    overflow: hidden;
    height:calc(100% - 3em);
    width:97.5%;
    font-size:0.94em
  }
  .overflow {
    overflow: hidden;
    width: 100%;
    height: calc(100% - 33px);
   // overflow-x: hidden;
  }
  .overflow .all_foot_ta tr{
    cursor:pointer;
  }
  .overflow .all_foot_ta tr:hover {
    background:#227fdf;
    color:white
  }
  .all_foot_ta tr td {
    width: 14.16%;
  }

  .hasSelect:after{
    content: '';
