  }
  .hasIcons{
    float: right;
    margin-right: 1em;
    display: block;
    margin-top: 0.8em;
    width: 2em;
    cursor: pointer;
    font-size: 1.35em;
  }

  .showAll{
     position:absolute;
     top:1.8em;
     right:0;
     height: calc(100% - 33px);
     width: calc(100% / 7 - 1px);
     border-left:1px solid #7B8390;
     z-index: 1;
     display:table;
     text-align:center;
